import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, ValidationErrors, Validators } from '@angular/forms';
import { AuthService } from '../core/auth.service';
import { UserService } from '../core/user.service';
import { ActivatedRoute } from '@angular/router'
import { User } from 'core/user.model';

// import { Observable, Observer } from 'rxjs';
@Component({
  selector: 'app-changepassword',
  templateUrl: './changepassword.component.html',
  styleUrls: ['./changepassword.component.scss'],
})
export class ChangepasswordComponent implements OnInit {

  email: string;
  user: User = new User()
  // validateForm: FormGroup;

  change = new FormGroup({
    email: new FormControl()
  })



  constructor(
    private fb: FormBuilder,
    private router: ActivatedRoute,
    public authService: AuthService,
    public userService: UserService,
  ) { }

  ngOnInit(): void {
    this.router.data.subscribe(async routeData => {
      let data = routeData['data']
      if (data) {
        this.user = await this.userService.getUserProfile(data.uid)
      } else {
        console.log('---errrr');
      }
    })



  }

  resetPassword(value, email: string) {
    this.email = value.email
    // email = this.user.email
    // email = value.email
    if (!this.email) {
      console.log('Khng c  trng ');
      document.getElementById('result2').innerHTML = "Khng c  trng nha! Cm n"
    } else {
      console.log('email');
      this.authService.ResetPassword(this.email)
        .then(res => {
          document.getElementById('result').innerHTML = "i thnh cng ri nha."
          console.log('------changepass', this.user);

        }, err => {
          document.getElementById('result2').innerHTML = " Email khng chnh xc vui lng kim tra li"
          console.log('email khng chnh xc vui lng kim tra li', err);

        })
    }
    console.log("===================", this.email);




  }

}
